#light .ItemFavoriets {
  max-width: 200px;
  position: relative;
  overflow: hidden;
  text-overflow: ellipsis;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid rgba(141, 141, 141, 0.2);
  box-shadow: 0px 10px 15px 0px rgba(0, 0, 0, 0.3);
  border-radius: 15px;
  cursor: pointer;
  background-color: white;
  color: grey;
  margin: 0;
  padding: 0;
}

#dark .ItemFavoriets {
  max-width: 200px;
  position: relative;
  overflow: hidden;
  text-overflow: ellipsis;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid rgba(141, 141, 141, 0.2);
  box-shadow: 0px 10px 15px 0px rgba(0, 0, 0, 0.3);
  border-radius: 15px;
  cursor: pointer;
  background-color: rgb(39, 46, 61);
  color: rgba(245, 245, 245, 0.8);
  margin: 0;
  padding: 0;
}

.ItemFavoriets-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 15px 50px 0;
}

.ItemFavoriets__title {
  max-width: 180px;
  font-family: 'Roboto',sans-serif;
  font-size: 1.5rem;
  line-height: 2rem;
  font-weight: 500;
  text-align: center;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
  margin: 0;
  padding: 0;
}

#light .ItemFavoriets__error {
  font-family: 'Roboto',sans-serif;
  font-size: 1rem;
  line-height: 1rem;
  font-weight: 400;
  text-align: center;
  color: black;
  margin: 20px 0;
  padding: 0;
}

#dark .ItemFavoriets__error {
  font-family: 'Roboto',sans-serif;
  font-size: 1rem;
  line-height: 1rem;
  font-weight: 400;
  text-align: center;
  color: rgba(255, 255, 255, 0.8);
  margin: 20px 0;
  padding: 0;
}

#light .ItemFavoriets__tempreture {
  font-family: 'Roboto',sans-serif;
  font-size: 2rem;
  line-height: 2.5rem;
  font-weight: 700;
  color: black;
  margin: 15px 0;
  padding: 0;
}

#dark .ItemFavoriets__tempreture {
  font-family: 'Roboto',sans-serif;
  font-size: 2rem;
  line-height: 2.5rem;
  font-weight: 700;
  color: rgba(255, 255, 255, 0.8);
  margin: 15px 0;
  padding: 0;
}

.ItemFavoriets__icon {
  width: 200px;
  margin: 0;
  padding: 0;
}

#light .ItemFavoriets__icon-description {
  font-family: 'Roboto',sans-serif;
  font-size: 1.5rem;
  line-height: 2rem;
  font-weight: 500;
  text-align: center;
  color: black;
  margin: 0;
  padding: 0;
}

#dark .ItemFavoriets__icon-description {
  font-family: 'Roboto',sans-serif;
  font-size: 1.5rem;
  line-height: 2rem;
  font-weight: 500;
  text-align: center;
  color: rgba(255, 255, 255, 0.8);
  margin: 0;
  padding: 0;
}

.ItemFavoriets__button {
  width: 100%;
  font-family: 'Roboto',sans-serif;
  font-size: 0.9rem;
  line-height: 2rem;
  font-weight: 700;
  color: black;
  border: none;
  border-radius: 0 0 15px 15px;
  background-color: rgba(240, 240, 240, 0.7);
  cursor: pointer;
  margin: 20px 0 0;
  padding: 5px;
  transition: background-color .1s ease-in-out;
}

.ItemFavoriets__button:hover {
  background-color: rgba(220, 220, 220, 0.7);
}

.ItemFavoriets__button:active {
  background-color: rgba(186, 186, 186, 0.7);
}

@media screen and (max-width:700px) {
  .ItemFavoriets-container {
    padding: 10px 30px 0;
  }
  
  .ItemFavoriets__title {
    font-size: 1.1rem;
    line-height: 1.5rem;
  }
  
  #light .ItemFavoriets__tempreture {
    font-size: 1.5rem;
    line-height: 2rem;
    margin: 10px 0;
  }
  
  #dark .ItemFavoriets__tempreture {
    font-size: 1.5rem;
    line-height: 2rem;
    margin: 10px 0;
  }
  
  .ItemFavoriets__icon {
    width: 100px;
    margin: 0;
    padding: 0;
  }
  
  #light .ItemFavoriets__icon-description {
    font-size: 1.1rem;
    line-height: 1.5rem;
  }
  
  #dark .ItemFavoriets__icon-description {
    font-size: 1.1rem;
    line-height: 1.5rem;
  }
}

@media screen and (max-width:550px) {
  .ItemFavoriets-container {
    padding: 10px;
  }
  
  .ItemFavoriets__title {
    max-width: 95px;
    font-size: 1rem;
    line-height: 1.5rem;
  }

  #light .ItemFavoriets__error {
    font-size: 0.8rem;
    line-height: 1rem;
  }
  
  #dark .ItemFavoriets__error {
    font-size: 0.8rem;
    line-height: 1rem;
  }
  
  #light .ItemFavoriets__tempreture {
    font-size: 1.1rem;
    line-height: 1.5rem;
    margin: 10px 0 0;
  }
  
  #dark .ItemFavoriets__tempreture {
    font-size: 1.1rem;
    line-height: 1.5rem;
    margin: 10px 0 0;
  }
  
  .ItemFavoriets__icon {
    width: 100px;
    margin: 0;
    padding: 0;
  }
  
  #light .ItemFavoriets__icon-description {
    font-size: 0.8rem;
    line-height: 1rem;
  }
  
  #dark .ItemFavoriets__icon-description {
    font-size: 0.8rem;
    line-height: 1rem;
  }

  .ItemFavoriets__button {
    font-size: 0.7rem;
    line-height: 1.5rem;
    margin: 5px 0 0;
  }
}